@import url('https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300&family=Ubuntu:wght@300&display=swap');
* {
    box-sizing: border-box;
  }
  body {
    display:block;
    margin: 0;
    padding: 0;
    /* font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
      Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif; */
    background: rgb(221,177,65);
    background: linear-gradient(90deg, rgba(221,177,65,1) 0%, rgba(226,123,71,1) 49%, rgba(249,119,167,1) 100%);
    height: 100%;
    width:100%;
  }